import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import React, { useEffect, useState } from 'react';
import AagnesLogoWhite from '../images/aagnes_logo_white.svg'
import { waitFor } from '../utils/Generic';

const HeroBanner = ({images}) => {

  const [currentImage, setCurrentImage] = useState(-1)

  const [topSrc, setTopSrc] = useState()
  const [bottomSrc, setBottomSrc] = useState()
  const [topVisible, setTopVisible] = useState(false)

  const [linksVisible, setLinksVisible] = useState(false)
  const [logoVisible, setLogoVisible] = useState(false)
  const [instaVisible, setInstaVisible] = useState(false)
  const [emailVisible, setEmailVisible] = useState(false)

  const [copied, setCopied] = useState(false)
  
  useEffect(() => {
    const initialLoading = async () => {
      setCurrentImage(currentImage => currentImage >= images.length - 1 ? 0 : currentImage + 1)
      await waitFor(1500)
      setLogoVisible(true)
      await waitFor(500)
      setLinksVisible(true)
      setInterval(() => {
        setCurrentImage(currentImage => currentImage >= images.length - 1 ? 0 : currentImage + 1)
      }, 6000);
    }
    initialLoading()
  }, [])

  useEffect(() => {
    const setImageSources = async () => {
      setTopSrc(getImage(images[currentImage]?.node.childImageSharp))
      await waitFor(2500)
      setTopVisible(true)
      await waitFor(2000)
      setBottomSrc(getImage(images[currentImage]?.node.childImageSharp))
    }

    setImageSources()
  }, [currentImage])

  useEffect(() => {
    setTopVisible(false)
  }, [bottomSrc])

  useEffect(() => {
    if(copied){
      setTimeout(() => {
          setCopied(false)
        },
      1000);
    }
  }, [copied])

  return(
    <div className="relative h-screen bg-black overflow-hidden">
      <div className='absolute h-full w-full'>
        {/* bottom */}
        <div className='absolute h-full'>
          <GatsbyImage
            image={bottomSrc}
            loading='eager'
            imgStyle={{
              objectFit: "cover",
              objectPosition: "50% 100%"
            }}
            className="h-full"
            alt=""
          />
        </div>
        {/* top */}
        <div 
          className={`absolute h-full transform ${topVisible ? 'transition-opacity' : 'transition-none'} duration-2000 ${topVisible ? 'opacity-100' : 'opacity-0'}`}
        >
          <GatsbyImage
            image={topSrc}
            loading='eager'
            imgStyle={{
              objectFit: "cover",
              objectPosition: "50% 100%"
            }}
            className="h-full"
            alt=""
          />
        </div>
      </div>
      <div
        className={`
          absolute h-full w-full flex flex-col justify-between items-start text-white font-serif text-sm 2xl:text-md ml-4 lg:ml-8 py-16
        `}
      >
        <div></div>
        <img
          src={AagnesLogoWhite}
          alt="Aagnes"
          className={`w-32 md:w-48 2xl:w-72 transform transition-opacity duration-1000 ${logoVisible ? 'opacity-100' : 'opacity-0'}`}
        />
        <ul
          className={`transform transition-opacity duration-1000 ${linksVisible ? 'opacity-100' : 'opacity-0'}`}
        >
          <li
            onMouseEnter={() => setInstaVisible(true)}
            onMouseLeave={() => setInstaVisible(false)}
          >
            <a
              aria-label="Instagram"
              href="https://www.instagram.com/____aagnes/"
              target="_blank"
              rel="noreferrer"
              className="flex transform transition-opacity opacity-50 duration-700 hover:opacity-100"
            >
              <h2>Ig</h2>
              <h3 className={`ml-3 transform transition-all duration-1000 ease-out ${!instaVisible ? '-translate-x-full opacity-0' : 'translate-x-0 opacity-100'}`}>@____aagnes</h3>
            </a>
          </li>
          <li
            onMouseEnter={() => setEmailVisible(true)}
            onMouseLeave={() => setEmailVisible(false)}
          >
            <button
              onClick={() => {
                navigator.clipboard.writeText('hola@aagnes.mx')
                setCopied(true)
              }}
              className="flex transform transition-opacity opacity-50 duration-700 hover:opacity-100"
            >
              <h2>@</h2>
              <h3 className={`ml-3 transform transition-all duration-1000 ease-out text-left ${!emailVisible ? '-translate-x-full opacity-0' : 'translate-x-0 opacity-100'}`}>hola@aagnes.mx <span className={`ml-4 lg:ml-6 transform transition-opacity duration-700 ease-in-out ${copied ? 'opacity-90' : 'opacity-0'} text-black`}>copied to clipboard</span></h3> 
            </button>
          </li>
        </ul>
      </div>
    </div>
  )
}

export default HeroBanner;